  .ajustesperfil{
    background-color: rgb(78, 78, 78);
  }

  .focus {
    box-shadow: none;
    border-color: #54b9cd;
    border-radius: 30px;
  }
  
  .profile-button {
    background: #54b9cd;
    box-shadow: none;
    border: none;
  }
  
  .profile-button:hover {
    background: #54b9cd;
  }
  
  .profile-button:focus {
    background: #54b9cd;
    box-shadow: none;
  }
  
  .profile-button:active {
    background: #54b9cd;
    box-shadow: none;
  }
  
  .back:hover {
    color: #682773;
    cursor: pointer;
  }
